import { ProLayout } from '@ant-design/pro-components';
import { useLocation, useNavigate } from '@modern-js/runtime/router';
import { useCallback, useEffect, useMemo } from 'react';
import type { MenuDataItem } from '@ant-design/pro-components';
import { ErrorBoundary } from '../ErrorBoundary';
import { MY_ROUTE, RoutePath } from './constants';
import { ReactComponent as IconLogo } from '@/assets/wrench.svg';

interface MyLayoutProps {
  defaultTerm?: number;
}

export const MyLayout: React.FC<React.PropsWithChildren<MyLayoutProps>> = ({
  children,
  defaultTerm,
}) => {
  const location = useLocation();
  const navigate = useNavigate();

  const renderMenuItem = useCallback(
    (item: MenuDataItem, dom: React.ReactNode) => {
      if (item.path === RoutePath.Copilots() && !defaultTerm) {
        return <div />;
      }
      return (
        <div
          onClick={e => {
            e.stopPropagation();
            if (typeof item.path === 'string') {
              switch (item.path) {
                case RoutePath.Copilots():
                  navigate(RoutePath.Copilots(defaultTerm));
                  break;
                case RoutePath.Solutions():
                  navigate(RoutePath.Solutions(defaultTerm));
                  break;
                default:
                  navigate(item.path);
                  break;
              }
            }
          }}
        >
          {dom}
        </div>
      );
    },
    [defaultTerm],
  );

  const handleClickLogo = useCallback(() => {
    navigate('/');
  }, []);

  useEffect(() => {
    const currentURL = new URL(window.location.href);
    if (
      currentURL.searchParams.has('code') &&
      currentURL.searchParams.has('state')
    ) {
      window.location.href = `${window.location.origin}/#${RoutePath.Upload}${currentURL.search}`;
    }
  }, []);

  const token = useMemo(() => {
    return { pageContainer: { paddingInlinePageContainerContent: 8 } };
  }, []);

  return (
    <ProLayout
      title="红油扳手作业站"
      logo={<IconLogo />}
      location={location}
      fixedHeader
      fixSiderbar
      route={MY_ROUTE}
      menuItemRender={renderMenuItem}
      onMenuHeaderClick={handleClickLogo}
      siderWidth={200}
      layout="mix"
      token={token}
      ErrorBoundary={ErrorBoundary}
    >
      {children}
    </ProLayout>
  );
};
